
/******* General default styling ********/
*, ::after, ::before {
 box-sizing: border-box;
}
html {
 width: 100%;
 height: 100%;
 font-size: 62.5%;
}
body {


  background: #f6f6f6;
  color: #333;
  font-size: 17px;
  padding: 5vh;
  height: 90vh;
  overflow: auto;
}

a {
 text-decoration: none;
 cursor: pointer;
}

li {
 list-style-type: none;
}
button {
 outline: none;
 cursor: pointer;
 border: none;
}
h1, a {
  font-family: "Love Ya Like A Sister", cursive;
}
 img {
  max-width: 100%;
 }
